.signup-form {
  .form-group {
    margin: 20px;
    .form-control {
      width: 100%;
      input {
        display: block;
        border: none;
        width: -webkit-fill-available;
        border-bottom: 1px solid #323c41;
        outline: none;
        margin: 10px 0;
        font-size: 16px;
        line-height: 2;
        font-family: "Raleway", sans-serif;
        padding: 10px 10px;
      }
      button {
        background-color: #2c3e50;
        text-align: center;
        color: white;
        font-family: Raleway, sans-serif;
        font-size: 16px;
        line-height: 2;
        width: 100%;
        padding: 10px 10px;
        border: 1px solid #cccccc;
        border-radius: 5px;
        cursor: pointer;
        &.disable {
          cursor: not-allowed;
        }
      }
    }
  }
  .login-link {
    display: flex;
    justify-content: center;
    p {
      width: fit-content;
      background-color: white;
      padding: 0px 5px;
      z-index: 10;
      text-align: center;
      color: #2c3e50;
      span {
        cursor: pointer;
        color: #1f344a;
        text-decoration: underline;
      }
    }
  }
  .divider {
    border-bottom: 2px solid black;
    position: absolute;
    width: 300px;
    margin-top: -32px;
  }
  .social-login {
    display: flex;
    flex-direction: column;
    button {
      text-align: center;
      color: white;
      font-family: Raleway, sans-serif;
      font-size: 16px;
      line-height: 2;
      padding: 10px 10px;
      border: 1px solid #cccccc;
      border-radius: 5px;
      cursor: pointer;
      i {
        font-size: 20px;
        padding-right: 20px;
      }
      &.facebook {
        background-color: #337ab7;
        width: 100%;
      }
      &.google {
        background-color: #d9534f;
      }
    }
  }
}
